kebal-6496463.jpg);\r\n    background-size: cover;\r\n    filter: blur(3px) brightness(0.7);\r\n    width: 100%;\r\n    height: 100%;\r\n    opacity: 0.25;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n}\r\n\r\n.side {\r\n    width: 50%;\r\n}\r\n\r\n.side.left {\r\n    position: relative;\r\n}\r\n\r\n.side .featured-bubble {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 23px;\r\n    transform: translateX(-50%);\r\n    position: absolute;\r\n    left: 100%;\r\n    top: 10%;\r\n    z-index: 1;\r\n}\r\n\r\n.homepage .wrapper-bubble-text {\r\n    transform: translate(-50%, -50%);\r\n    top: 24%;\r\n    left: 50%;\r\n}\r\n\r\n.homepage .bubble-action {\r\n    transform: translate(50%, -50%);\r\n    top: 100%;\r\n    right: 50%;\r\n}\r\n\r\n.homepage .bubble-action:hover {\r\n    transform: translate(50%, -50%) scale(1.08);\r\n}\r\n\r\n.side.right {\r\n    padding: 30px;\r\n    position: relative;\r\n}\r\n\r\n.side.right::after {\r\n    content: '';\r\n    background: $main-gradient;\r\n    width: 100%;\r\n    height: 100%;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: -1;\r\n    animation: banner_animation-after 30s ease-in-out infinite;\r\n}\r\n\r\n.side.right::before {\r\n    content: '';\r\n    background: $main-gradient-alt;\r\n    width: 100%;\r\n    height: 100%;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: -1;\r\n}\r\n\r\n.side .container-social {\r\n    display: flex;\r\n    gap: 13px;\r\n    align-items: center;\r\n    justify-content: right;\r\n}\r\n\r\n.side .container-social a {\r\n    z-index: 1;\r\n}\r\n\r\n.side .container-social svg {\r\n    cursor: pointer;\r\n    fill: $dark-text_color;\r\n    transition: 0.3s;\r\n}\r\n\r\n.side .container-social svg:hover {\r\n    transform: scale(1.2);\r\n}\r\n\r\n.side .container-title {\r\n    transform: translate(-50%, -50%);\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 50%;\r\n}\r\n\r\n.side .container-title .ti